.nav {
  z-index: 1;
  position: fixed;
  top: 50px;
  left: 10px;
  display: flex;
  flex-direction: column;
  background-color: none;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 5px 16px -5px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  border-radius: 5px;
}

.nav .divisionLine {
  height: 1px;
  background-color: lightgrey;
  border-width: 0px;
  margin: 0px;
}
  
@media screen and (max-width: 1090px) {
  .nav {
    left: initial;
    top: initial;
    bottom: 0px;
    width: 100%;
  }
}

.nav .nav_btn {
  background-color: white;
  text-decoration: none;
  color: black;
  text-transform: uppercase;
  font-size: 12px;
  text-align: center;
  font-weight: 600;
  padding: 10px 20px;
  border: none;
  margin: 0px;
  cursor: pointer;
}

.nav_btn:hover {
  background: lightgray;
  color: white;
}

.nav .nav_home {
  border-radius: 5px 5px 0px 0px;
}

.nav .nav_last {
  border-radius: 0px 0px 5px 5px;
}
.movies a {
    text-decoration: none;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.movie {
    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    background-color: #FAFAFA;
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #6C6C6C;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .coverImg {
    position: relative;
    top: -38px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 4px 4px 15px 3px rgb(0 0 0 / 25%);
}

.movie .title {
    color: black;
    margin: 0px 0px;
}

.movie .info {
    margin: 3px 0px 0px 0px;
}

.movie .genres {
    padding: 0px;
    list-style: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.movie .genre {
    margin-right: 8px;
}

.movie .year {
    display: block;
}

.ratingStar {
    color: #FFC700;
    margin-right: 3px;
}

.movie .summary {
    line-height: 20px;
    display: block;
}
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.movies {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 90%;
    padding-top: 70px;
}

@media screen and (max-width: 1090px) {
    .movies {
      grid-template-columns: 1fr;
      width: 100%;
    }
  }
.detailContainer {
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: flex-start;
    width: 70%;
    margin: 20px auto;
}

.detail {
    margin: 5px 0;
    color: #6a6a6a;
}

.btnGoSite {
    display: block;
    margin: 5px auto;
    padding: 15px 30px;
    border-radius: 15px;
    font-family: "paybooc-Light", sans-serif;
    box-shadow: 0 1px 20px rgb(0 0 0 / 20%);
    text-decoration: none;
    font-weight: 600;
    background-color: aliceblue;
    color: #1e6b7b;
    text-align: center;
}

.btnGoSite:hover {
    background-color: #1e6b7b;
    color: aliceblue;
}

.detailContainer .detailInfo {
    padding: 0px 20px;
}

.detailContainer .detailTitle {
    margin: 0 0 10px 0;
    font-size: 30px;
}

.detailContainer .detailGenres {
    padding: 0px;
    list-style: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.detailContainer .detailGenre {
    margin-right: 8px;
}
* {
    box-sizing: border-box;
}
  
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    background-color: #eff3f7;
    height: 100%;
}

.loadingWrapper {
    text-align: center;
}

.loading {
    font-size: 35px;
}
